body {
  margin: 0;
  padding: 0;
  font-family: Source Sans Pro, sans-serif;
}



.game-section {
	border: 2px solid #344861;
	height: 10%;
	width: 23%;
	display: inline-block;
}


.section-row {
    padding: 0;
    margin: 0;
    width: 100%;
}
.game-cell {
    flex-basis: 5%;
    box-sizing: border-box;
    position: relative;
    border-right: 1px solid #bec6d4;
    border-bottom: 1px solid #bec6d4;
    cursor: pointer;
    transform: translateZ(0);
    padding: 0;
    margin: 0;
    border-collapse: collapse;
    border-spacing: 0;
    display: inline-block;
    width: 33.3%;
    height: 33.3%;
}

.game-cell .cell-value { 
    font-weight: 400;
    color: #000;
    cursor: default; 
}

.cell-value {
    width: 100%;
    height: 100%;
    font-weight: 100;
    color: #666;
    text-align: center;
}
@-webkit-keyframes load {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@keyframes load {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.spinner {
	border: 4px solid rgba(255, 255, 255, 0.2);
	border-left: 4px solid;
	-webkit-animation: load 1s infinite linear;
	        animation: load 1s infinite linear;
	border-radius: 50%;
	width: 25px;
	height: 25px;
	margin-top: -5px;
	margin-left: 5px;
}

.btn {
	display: inline-block;
	position: relative;
	margin-bottom: 20px;
	padding: 15px 24px 17px;
	box-sizing: border-box;
	background: #264653;
	font-family: SourceSansPro, 'Helvetica Neue', Helvetica, Arial,
		'Lucida Grande', sans-serif;
	color: #fff;
	font-size: 19px;
	font-size: 1.0556rem;
	line-height: 19px;
	font-weight: 700;
	text-align: center;
	text-decoration: none;
	letter-spacing: 0.25px;
	cursor: pointer;
	border: 1px solid #009845;
	border-radius: 0;
	transition: background 0.2s ease-in-out, color 0.2s ease-in-out,
		border 0.2s ease-in-out;
}

.btn-small {
	font-size: 12px;
	font-size: 1.0556rem;
	font-weight: 400;
	padding: 10px 10px 10px;
}

.btn.btn-close {
	background: #34507e;
}

.circle {
	width: 40px;
	height: 40px;
	text-align: center;
	font-size: 28px;
	line-height: 1.428571429;
	border-radius: 35px;
	color: #ffffff;
	background-color: #428bca;
	border-color: #48a50a;
	font-weight: 800;
	background: #2a9d8f;
}

.circle.lg {
	width: 50px;
	height: 50px;
	padding: 10px 16px;
	font-size: 18px;
	line-height: 1.33;
	border-radius: 25px;
}

.steps-log {
	border: 2px solid #344861;
	height: 10%;
	width: 69%;
	height: 37%;
	display: inline-block;
	overflow: auto;
}

.steps-log .title {
	margin-top: 3px;
	font-size: 18px;
}

.steps-log .intro {
	text-align: left;
	font-size: 13px;
	padding-left: 5px;
	padding-right: 5px;
	margin-bottom: 3px;
}


.log-detail-row {
	border-top: 1px solid black;
	background: white;
	padding-bottom: 5px;
}

.log-detail-row .title {
	text-align: left;
	font-size: 13px;
	padding-left: 5px;
	padding-right: 5px;
	font-weight: bold;
}

.log-detail-row .details {
	text-align: left;
	font-size: 13px;
	padding-top: 5px;
	padding-left: 5px;
	padding-right: 5px;
}

.Modal {
	position: fixed;
	z-index: 500;
	background-color: white;
	width: 70%;
	border: 1px solid #ccc;
	box-shadow: 1px 1px 1px black;
	padding: 16px;
	left: 15%;
	top: 30%;
	box-sizing: border-box;
	transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
	.Modal {
		width: 500px;
		left: calc(50% - 250px);
	}
}

.backdrop {
	width: 100%;
	height: 100%;
	position: fixed;
	z-index: 100;
	left: 0;
	top: 0;
	background-color: rgba(0, 0, 0, 0.5);
}

.action-buttons {
	text-align: right;
	display: inline-block;
}

input {
	width: 100%;
	display: inline;
	min-height: 35px;
	padding: 5px 12px;
	font-size: 16px;
	/* line-height: 1.5; */
	color: #555555;
	vertical-align: middle;
	background-color: #ffffff;
	border: 1px solid #cccccc;
	border-radius: 2px;
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
	z-index: 2000;

	text-rendering: auto;
	color: -internal-light-dark(black, white);
	letter-spacing: normal;
	word-spacing: normal;
	text-transform: none;
	text-indent: 0px;
	text-shadow: none;
	display: inline-block;
	text-align: start;
	background-color: -internal-light-dark(rgb(255, 255, 255), rgb(59, 59, 59));
	cursor: text;
	margin: 0em;
	font: 400 13.3333px Arial;
	padding: 1px 2px;
	border-width: 2px;
	border-style: inset;
	border-color: -internal-light-dark(rgb(118, 118, 118), rgb(195, 195, 195));
	border-image: none;
	border-image: initial;
}

input::-webkit-input-placeholder {
	padding: 10px;
}

input::-ms-input-placeholder {
	padding: 10px;
}

input::placeholder {
	padding: 10px;
}

.board-text-input {
	background: #ffffff;
	border-radius: 0px;
	background-clip: padding-box;
	box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.1);
	box-shadow: inset 0px 0px 0px rgba(0, 0, 0, 0.5);
	border: solid 1px #c4cfda;
	margin-bottom: 12px;
}

.new-board .errors {
	display: inline-block;
	min-width: 70%;
	max-width: 70%;
}

.abort-board {
	color: #e2e221;
}


.solved-board {
	color: #2a9d8f;
}

.unsolved-board {
	color: red;
}

.timer-solve {
	color: blue;
}

.App {
	text-align: center;
	font-family: SourceSansPro, 'Helvetica Neue', Helvetica, Arial,
		'Lucida Grande', sans-serif;
}

.App-logo {
	height: 40px;
	padding-left: 10px;
}

.App-author {
	padding-right: 16px;
}

.App-status-message {
	line-height: 1.5;
	display: inline-block;
	margin-left: 10px;
}

.App-author-image {
	height: 45px;
}

.App-message-row {
	text-align: left;
	padding-left: 55px;
	height: 50px;
}

.App-status-abort-message {
	display: inline-block;
	font-size: 13px;
	width: 75%;
	padding-left: 15px;
	font-weight: 500;
	text-align: left;
}

.App-icon {
	font-size: 1.5em;
	display: inline-block;
}

.App-icon .invalid {
	width: 30px;
}

.App-header {
	width: 100%;
	max-width: 1504px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 16px;
	padding-right: 16px;
	box-sizing: border-box;
	border-bottom: 1px solid rgba(198, 206, 217, 0.5);
	display: flex;
	align-items: center;
}

.App-title {
	flex: 1 1;
	font-size: 1.5em;
	align-items: center;
	padding-left: 50px;
	padding-right: 50px;
}

.App-body {
	background-color: #f3f5f8;
	font-size: large;
	padding-left: 16px;
	display: flex;
	height: 100vh;
}

.App-game-panel {
	display: inline-block;
	padding-top: 25px;
	padding-bottom: 25px;
	width: 30%;
	margin-top: 1em;
	margin-bottom: 2em;
	margin-right: 1em;
	background-color: #ffffff;
	border: 1px solid #eee;
	box-shadow: 0 2px 2px #ccc;
}

.App-game-panel .buttons-row {
	margin-top: 20px;
}

@-webkit-keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.marginLeft10 {
	margin-left: 10px;
}

.marginBottom10 {
	margin-bottom: 10px;
}

.marginTop10 {
	margin-top: 10px;
}

.marginTop15 {
	margin-top: 15px;
}

.center-me {
	margin: 0 auto;
}

